import axios from "axios";
const headers = {
    'web-flag':"wexin"
}
const baseUrl = "/mso"
// ;

// md
const getIntroMd = (param)=>{
    const url = baseUrl + '/file/getChapters';
    return axios.get(url,{
        headers:headers,
        params:param
    }).then((res)=>{
        return Promise.resolve(res)
    }).catch((error)=>{
        return Promise.reject(error);
    })
}

